import { Component, OnInit } from '@angular/core';
import * as shape from 'd3-shape';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})


export class AppComponent implements OnInit {

  title = 'packer';

  hierarchialGraph = {nodes: [], links: []}
  curve: any = shape.curveBundle.beta(1);
  colourScheme: string = "nightLights"
  orientation: string  = "TB"
  width: number = 1400;
  height: number = 700;
  view:any = [this.width, this.height];
  panOffsetX: number = 100;

  public ngOnInit():void {
    this.showGraph();
  }

  showGraph() {

    this.hierarchialGraph.nodes = [
      {id: 'start', label: 'Template.json'},
      {id: '1',     label: 'Azure'}, 
      {id: '2',     label: 'AWS EC2'},
      {id: '3',     label: 'Google Cloud'},

      {id: '10',    label: 'File'},  
      {id: '11',    label: 'PowerShell'},  
      {id: '12',    label: 'Windows Shell'}, 
      {id: '13',    label: 'Windows Restart'}, 

      {id: '20',    label: 'OS Image'},  

      {id: '30',    label: 'Post-Processors'},  
    ];

    this.hierarchialGraph.links = [
      { source: 'start', target: '1',  label: 'builder'}, 
      { source: 'start', target: '2',  label: 'builder'}, 
      { source: 'start', target: '3',  label: 'builder'}, 

      { source: '1', target: '10'},
      { source: '1', target: '11'},
      { source: '1', target: '12'},
      { source: '1', target: '13'},

      { source: '2', target: '10'},
      { source: '2', target: '11'},
      { source: '2', target: '12'},
      { source: '2', target: '13'},

      { source: '3', target: '10'},
      { source: '3', target: '11'},
      { source: '3', target: '12'},
      { source: '3', target: '13'},

      { source: '10', target: '20'},
      { source: '11', target: '20'},
      { source: '12', target: '20'},
      { source: '13', target: '20'},

      { source: '20', target: '30'},
    ];
  }

}